import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from 'src/app/service/product.service';
declare var swal: any;

@Component({
  selector: 'app-offer-product-add',
  templateUrl: './offer-product-add.component.html'
})
export class OfferProductAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  offerList: any = [];
  productList:any=[];
  showLoader = false;
  formErrors = {
    product_id: null,
    apierror: null
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private ProductService: ProductService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      product_id: [null, Validators.compose([Validators.required])],
      offer_price: [null, Validators.compose([Validators.required])],
      vendors_offers_id: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.ProductService.getOfferProductById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            product_id: response.data.product_id,
            offer_price: response.data.offer_price,
            vendors_offers_id: response.data.vendors_offers_id,
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/bankinfo');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].product_id.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.ProductService.editOfferProduct(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/bankinfo');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }

            });
          }
        });
      } else {
        this.ProductService.addOfferProduct(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/bankinfo');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            });
          }
        });
      }
    }
  }
}
